import cn from 'classnames';
import React, {useEffect, useState} from 'react';
import {createRoot} from 'react-dom/client';
import {Spacer} from 'react-shared/components/Spacer';
import {Body, Heading, Label} from 'react-shared/components/Text';
import {LicenseAttribution, LicenseAttributionList} from 'vscode-shared/ai-recitation';
import {Citation} from '~common/messaging/modules/aichat/aichat-protocol';
import styles from './airecitationlog.entry.scss';
import {PreCodeSnippet, PreCodeSnippetContext} from './components/PreCodeSnippet';
import {createHtmlBroker} from './globals/html-broker';
import {Icon} from 'react-shared/components/Icon';

const vscode = (window as any)['acquireVsCodeApi']();
const EXTENSION_ROOT_URI = (window as any)['EXTENSION_ROOT_URI'];
const RECITATION_TOKEN = (window as any)['RECITATION_TOKEN'];
const broker = createHtmlBroker(vscode);

function App() {
  const [recitationItems, setRecitationItems] = useState<LicenseAttribution[]>([]);
  const [focusId, setFocusId] = useState<string>('');

  useEffect(() => {
    broker.send('getRecitationLog', RECITATION_TOKEN);
    broker.on('notifyRecitationLog', (log: LicenseAttributionList, focusId: string) => {
      setRecitationItems(log.attributions);
      setFocusId(focusId);
    });
  }, []);

  useEffect(() => {
    if (!focusId) return;
    let timeout = setTimeout(() => setFocusId(''), 2500);
    return () => clearTimeout(timeout);
  }, [focusId]);

  if (!recitationItems.length) {
    return (
      <div className={styles.root}>
        <div className={styles.zeroState}>
          <Icon className={styles.icon} icon="tag" />
          <Spacer size="medium" />
          <Body secondary>No license references in this workspace yet</Body>
        </div>
      </div>
    );
  }

  return (
    <PreCodeSnippetContext shikiRootUri={EXTENSION_ROOT_URI + '/shiki'} broker={broker}>
      <div className={styles.root}>
        <Body secondary className={styles.header}>
          Below is a record of possible license references added to your code while using Gemini for
          code assistance in this workspace.{' '}
          <a href="http://g.co/legal/generative-code">Learn more</a>
        </Body>
        {recitationItems.map((item, idx) => (
          <div
            key={item.id}
            id={item.id}
            ref={node => {
              if (focusId !== item.id || !node) {
                return;
              }
              node.scrollIntoView({behavior: 'instant', block: 'nearest'});
            }}
            className={cn(styles.recitationItem, {[styles.highlightFlash]: focusId === item.id})}
          >
            <Heading level={2} selectable>
              {new Intl.DateTimeFormat(undefined, {
                dateStyle: 'full',
                timeStyle: 'long',
              }).format(new Date(item.timestamp))}
            </Heading>
            <Spacer size="xsmall" />
            <Label level={3} selectable secondary>
              Reference #{item.id}
            </Label>
            <Spacer size="large" />
            <PreCodeSnippet
              code={item.snippet}
              citations={[
                {
                  startIndex: 0,
                  endIndex: 0,
                  license: item.license,
                  repository: item.repository,
                  uri: item.uri,
                } satisfies Citation,
              ]}
            />
          </div>
        ))}
      </div>
    </PreCodeSnippetContext>
  );
}

function formatRepoUrl(url: string) {
  return url.replace(/^https?:\/\//, '').replace(/^github\.com\//, '');
}

const root = createRoot(document.getElementById('root')!);
root.render(<App />);
