import cn from 'classnames';
import {diffLines} from 'diff';
import flourite from 'flourite'; // we previously used highlight.js for language detection
import React, {useCallback, useContext, useEffect, useMemo, useRef, useState} from 'react';
import {Icon} from 'react-shared/components/Icon';
import {IconButton} from 'react-shared/components/IconButton';
import {Body, Label} from 'react-shared/components/Text';
import * as shiki from 'shiki';
import {Citation} from '~common/messaging/modules/aichat/aichat-protocol';
import {HtmlBrokerImpl} from '~webviews/globals/html-broker';
import styles from './PreCodeSnippet.scss';

let codeLanguageCache: {[code: string]: string} = {};

const ThemeContext = React.createContext({
  highlightTheme: '',
  getHighlighter: (lang: string) =>
    Promise.resolve({
      highlighter: null,
      lang,
    } as {
      highlighter: shiki.Highlighter | null;
      lang: string;
    }),
});

export function PreCodeSnippet({
  code,
  diffAgainstCode,
  borderless,
  lang,
  citations,
  startCollapsed,
  onInsertCode,
  onCopyCode,
  className,
  ...props
}: any) {
  let isCollapsible = !!startCollapsed && code.replace(/\n$/, '').split('\n').length > 3;
  let [isCollapsed, setCollapsed] = useState(isCollapsible);
  let [autoDetectedLang, setAutoDetectedLang] = useState<string>('');
  let preNode = useRef<HTMLPreElement | null>(null);
  let resolvedLang = lang || autoDetectedLang;
  let {highlightTheme, getHighlighter} = useContext(ThemeContext);

  // if this is a diff, compute diff information
  let diffInfo = useMemo(() => {
    let lineOptions: {line: number; classes?: string[]}[] = [];
    if (diffAgainstCode === undefined) {
      return {};
    }
    let lines: string[] = [];
    let lineNum = 1;
    if (!diffAgainstCode.endsWith('\n')) {
      diffAgainstCode += '\n';
    }
    if (!code.endsWith('\n')) {
      code += '\n';
    }
    for (let change of diffLines(diffAgainstCode, code)) {
      lines.push(change.value);
      let classes: string[] = [styles.diffLineSame];
      if (change.added) {
        classes = [styles.diffLineAdded];
      } else if (change.removed) {
        classes = [styles.diffLineRemoved];
      }
      for (let i = 0; i < (change.count || 0); i++) {
        lineOptions.push({line: lineNum + i, classes});
      }
      lineNum += change.count || 0;
    }
    return {oldAndNewCode: lines.join(''), lineOptions};
  }, [diffAgainstCode, code]);

  useEffect(() => {
    if (!highlightTheme || !preNode.current || !code || !resolvedLang) {
      return;
    }
    (async () => {
      let {highlighter, lang} = await getHighlighter(resolvedLang);
      if (!highlighter) {
        // ensure we don't show both old AND new code if this is a diff.
        if (diffInfo) {
          preNode.current!.innerHTML = code;
        }
        return;
      }

      let html = (highlighter as shiki.Highlighter)
        .codeToHtml(diffInfo.oldAndNewCode ?? code, {
          lang,
          theme: highlightTheme,
          lineOptions: diffInfo.lineOptions || [],
        })
        .replace(/<\/?(pre|code).*?>/g, '');
      preNode.current!.innerHTML = html;
    })();
  }, [code, diffInfo, resolvedLang, highlightTheme]);

  // Autodetect lang if needed
  useEffect(() => {
    setAutoDetectedLang('');
    if (lang) {
      return; // language already provided
    }
    if (codeLanguageCache[code]) {
      setAutoDetectedLang(codeLanguageCache[code]);
      return;
    }

    // autodetect
    let autoLang = autoDetectLanguageWithFlourite(code) || '';
    codeLanguageCache[code] = autoLang;
    setAutoDetectedLang(autoLang);
  }, [code, lang]);

  return (
    <div
      className={cn(className, styles.codeSnippet, {
        [styles.isCollapsed]: isCollapsed,
        [styles.isBorderless]: !!borderless,
        [styles.isDiff]: !!diffInfo,
      })}
      onClick={!isCollapsed ? undefined : () => setCollapsed(false)}
    >
      <div className={styles.preContainer}>
        <pre ref={preNode} {...props}>
          {diffInfo.oldAndNewCode ?? code}
        </pre>
        {!isCollapsed && isCollapsible && (
          <Body level={2}>
            <a
              className={styles.collapseLink}
              href="javascript:null"
              role="button"
              onClick={ev => {
                ev.preventDefault();
                setCollapsed(true);
              }}
            >
              See less
            </a>
          </Body>
        )}
        {!!citations?.length && (
          <>
            <Label level={3} secondary className={styles.citations}>
              <Icon icon="tag" />
              <span>
                Suggested code may be subject to a license:
                {citations.map((citation: Citation) => (
                  <React.Fragment
                    key={`${citation.startIndex}-${citation.endIndex}-${citation.repository}`}
                  >
                    &nbsp;&nbsp;
                    <CitationItem citation={citation} />
                  </React.Fragment>
                ))}
              </span>
            </Label>
          </>
        )}
      </div>

      {!isCollapsed && (
        <div className={styles.codeSnippetToolbar}>
          {lang || autoDetectedLang}
          {autoDetectedLang && ' (auto)'}
          {onInsertCode && (
            <IconButton tooltip="Insert Code" icon="insert" onClick={() => onInsertCode()} />
          )}
          <IconButton
            tooltip="Copy to Clipboard"
            icon="copy"
            onClick={() => {
              onCopyCode?.();
              navigator.clipboard.writeText(code);
            }}
          />
        </div>
      )}
    </div>
  );
}

function autoDetectLanguageWithFlourite(codeInput: string | string[]): string | null {
  let code = '';
  if (codeInput instanceof Array) {
    if (codeInput.length == 0) {
      return null;
    }
    code = codeInput[0];
  } else {
    code = codeInput;
  }

  // see https://github.com/teknologi-umum/flourite/blob/master/src/shiki.ts
  function flouriteToShiki(language: string): string {
    if (language === 'C++') {
      return 'cpp';
    }
    if (language === 'C#') {
      return 'csharp';
    }
    return language.toLowerCase();
  }
  let detectedLang = Object.entries(flourite(code, {shiki: true, noUnknown: true}).statistics)
    .map(([lang, score]) => [flouriteToShiki(lang), score] as [string, number])
    .filter(
      ([lang]) => lang !== 'elixir' && lang !== 'julia' && lang !== 'clojure' && lang !== 'pascal'
    )
    .sort(([, aScore], [, bScore]) => bScore - aScore);
  return detectedLang[0]?.[0] || null;
}

