function CitationItem({citation}: {citation: Citation}) {
  let {uri, license, repository} = citation || {};
  let [, repo] = (repository || '').split('/');
  if (!uri && repository) {
    uri = `https://github.com/${repository}`;
  }
  return (
    <>
      {!!uri && <a href={uri}>{repo}</a>}
      {!uri && <span>{repository}</span>}
      {!!license && <> ({license})</>}
    </>
  );
}

export function PreCodeSnippetContext({
  shikiRootUri,
  broker,
  children,
}: {
  shikiRootUri: string;
  broker: HtmlBrokerImpl;
  children: React.ReactNode;
}) {
  // for syntax highlighting
  const [themeObj, setThemeObj] = useState<shiki.IShikiTheme | null>(null);
  const [editorFontFamily, setEditorFontFamily] = useState<string>('');
  const shikiHighlighter = useRef<shiki.Highlighter | null>(null);
  const [highlightTheme, setHighlightTheme] = useState('');

  let getHighlighter = useCallback(async (lang: string) => {
    while (!shikiHighlighter.current) {
      await new Promise(resolve => setTimeout(resolve, 250));
    }

    if (shikiHighlighter.current.getLoadedLanguages().indexOf(lang as shiki.Lang) < 0) {
      try {
        await shikiHighlighter.current.loadLanguage(lang as shiki.Lang);
      } catch (e) {
        console.warn(`No shiki highlighter for language: ${lang}, using fallback`);
        lang = 'plaintext';
      }
    }

    return {
      highlighter: shikiHighlighter.current,
      lang,
    };
  }, []);

  useEffect(() => {
    (async () => {
      shikiHighlighter.current = await shiki.getHighlighter({
        theme: {} as shiki.IShikiTheme,
        langs: [], // loaded later
        paths: {
          languages: shikiRootUri + '/languages/',
          wasm: shikiRootUri + '/wasm/',
        },
      });
    })();
  }, []);

  useEffect(() => {
    if (editorFontFamily) {
      document.body.style.setProperty('--editor-font-family', editorFontFamily);
    } else {
      document.body.style.removeProperty('--editor-font-family');
    }
  }, [editorFontFamily]);

  useEffect(() => {
    if (!shikiHighlighter.current || !themeObj) {
      return;
    }
    (async () => {
      await shikiHighlighter.current!.loadTheme(themeObj as shiki.IShikiTheme);
      setHighlightTheme(themeObj!.name);
    })();
  }, [shikiHighlighter.current, themeObj]);

  useEffect(() => {
    broker.send('getTheme');
    broker.on('notifyTheme', (themeObj, editorFontFamily) => {
      setThemeObj(themeObj);
      setEditorFontFamily(editorFontFamily);
    });
  }, []);

  return (
    <ThemeContext.Provider value={{getHighlighter, highlightTheme}}>
      {children}
    </ThemeContext.Provider>
  );
}
